import {Component} from "@angular/core";
import {CsdService} from "@generated/csd-service";
import {VsanUiUtils} from "@util/vsan-util";
import {UnmountRemoteDatastoreContext} from "@navigation/context/unmount-remote-datastore-context";
import {ManagedObject} from "@service/managed-object";

@Component({
   selector: "vsan-mount-remote-datastore-wizard",
   template: `
      <vsan-delete-prompt [title]="'vsan.csd.unmountDialog.title' | localized"
                          [subtitle]="context.datastore.name"
                          [confirmText]="'vsan.csd.unmountDialog.confirmButton' | localized"
                          (onConfirm)="unmount()"
                          [(alertMessages)]="errors"
                          [allowAlertClose]="false"
                          [blockButton]="blockButton"
                          [blockContent]="blockButton">
         <p>{{"vsan.csd.unmountDialog.confirmText" | localized : context.datastore.name}}</p>
      </vsan-delete-prompt>
   `
})
export class UnmountRemoteDatastoreModalComponent {
   context: UnmountRemoteDatastoreContext = VsanUiUtils.getModalContext();

   errors: string[] = [];
   blockButton: boolean = false;

   constructor(private csdService: CsdService) {
   }

   async unmount() {
      try {
         this.blockButton = true;
         const unmountTask: ManagedObject = await this.csdService.unmountRemoteDatastore(
               this.context.clusterRef, this.context.datastore.moRef);
         VsanUiUtils.closeModalDialog(unmountTask);
      } catch (err) {
         this.errors.push(err);
      }
   }
}
