import {Component, OnInit} from "@angular/core";
import {UnavailableViewSpec} from "@component/unavailable-view/unavailable-view-spec";
import {CsdService} from "@generated/csd-service";
import {MountedRemoteDatastore} from "@generated/mounted-remote-datastore";
import {PermissionService} from "@generated/permission-service";
import {Privileges} from "@generated/privileges";
import {VsanCapabilityProvider} from "@generated/vsan-capability-provider";
import {NavigationService, PlatformExtension} from "@navigation/navigation.service";
import {ClusterComputeResource} from "@service/client/dataservice/data-service-property";
import {DataServiceClient} from "@service/client/dataservice/data-service.service";
import {ErrorMessage} from "@service/flow/error-message.decorator";
import {Loader} from "@service/flow/loader.decorator";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {RefreshHandler} from "@service/global-refresh.service";
import {ManagedObject} from "@service/managed-object";
import {TaskMonitorService} from "@service/task-monitor-service";
import {Logger} from "@util/logger";
import {ModalBuilder} from "@util/modal-builder";
import {VsanUiUtils} from "@util/vsan-util";

@Component({
   templateUrl: "datastore-sharing.html",
   styleUrls: ["datastore-sharing.scss"],
   selector: "vsan-datastore-sharing",
})
export class DatastoreSharingComponent implements OnInit {
   readonly ManagedObject: typeof ManagedObject = ManagedObject;

   @LoadingIndicator()
   loading: boolean;
   @ErrorMessage()
   errorMessage: string;
   isCsdSupportedOnCluster: boolean = false;
   unavailableView: UnavailableViewSpec = null;
   hasEditClusterPermission: boolean = false;
   csdNotSupportedMessage: string = VsanUiUtils.getString("vsan.csd.notSupportedOnCluster");

   datastores: MountedRemoteDatastore[] = [];
   selectedDatastore: MountedRemoteDatastore = null;

   constructor(
         private capabilityProvider: VsanCapabilityProvider,
         private dataService: DataServiceClient,
         private permissionService: PermissionService,
         private csdService: CsdService,
         private navigationService: NavigationService,
         private taskMonitorService: TaskMonitorService) {
   }

   async ngOnInit(): Promise<void> {
      this.isCsdSupportedOnCluster = await this.checkCsdSupportedOnCluster();
      await Promise.all([
         this.fetch(),
         this.permissionService.hasPermissions(ManagedObject.contextObject, [
            Privileges.EDIT_CLUSTER(), Privileges.CONFIG_STORAGE(),
         ]).then(res => this.hasEditClusterPermission = res),
      ]);
   }

   @RefreshHandler
   @Loader({srMessage: "vsan.csd.datastoreSharing.title"})
   async fetch(): Promise<void> {
      await this.checkNumberOfHosts();
      if (this.isCsdSupportedOnCluster) {
         await this.fetchMountedDatastores();
      }
   }

   @Loader()
   async fetchMountedDatastores(): Promise<void> {
      const previouslySelectedDatastore: MountedRemoteDatastore = this.selectedDatastore;
      this.datastores = await this.csdService.getMountedDatastores(ManagedObject.contextObject);
      if (previouslySelectedDatastore) {
         this.selectedDatastore = this.datastores.find(ds =>
               ManagedObject.areEqual(ds.shareableDatastore.datastore.moRef,
                     previouslySelectedDatastore.shareableDatastore.datastore.moRef));
      }
   }

   @Loader()
   private async checkCsdSupportedOnCluster(): Promise<boolean> {
      return await this.capabilityProvider.getIsCsdSupported(ManagedObject.contextObject);
   }

   @Loader()
   private async checkNumberOfHosts(): Promise<void> {
      const numberOfHosts: number = await this.dataService
            .getProperty(ClusterComputeResource.host.length, ManagedObject.contextObject);
      if (!numberOfHosts) {
         this.unavailableView = UnavailableViewSpec.DATASTORE_SHARING_VIEW_NO_HOSTS;
      } else {
         this.unavailableView = null;
      }
   }

   navigateToMor(morRef: ManagedObject): void {
      this.navigationService.navigateTo(PlatformExtension.LAST_VISITED_PAGE, morRef);
   }

   navigateToDatastoreCapacity(datastoreRef: ManagedObject): void {
      this.navigationService.navigateTo(PlatformExtension.DATASTORE_CAPACITY, datastoreRef);
   }

   isUnmountEnabled(): boolean {
      return this.hasEditClusterPermission &&
            this.selectedDatastore &&
            !this.selectedDatastore.isLocal &&
            !this.selectedDatastore.registeredVMCount;
   }

   get mountDatastoreTooltip(): string {
      return this.hasEditClusterPermission ? "" : VsanUiUtils.getString("vsan.common.nopermissions");
   }

   get unmountDatastoreTooltip(): string {
      if (!this.hasEditClusterPermission) {
         return VsanUiUtils.getString("vsan.common.nopermissions");
      }
      if (!this.isUnmountEnabled() && !!this.selectedDatastore) {
         if (this.selectedDatastore.isLocal) {
            return VsanUiUtils.getString("vsan.csd.datastoreSharing.actions.unmountLocalDatastoreImpossible");
         }
         return VsanUiUtils.getString("vsan.csd.datastoreSharing.actions.vmBlocksUnmount");
      }
      return "";
   }

   isClusterLocal = (clusterRef: ManagedObject): boolean =>
         ManagedObject.areEqual(clusterRef, ManagedObject.contextObject);

   async openUnmountDialog(): Promise<void> {
      const modalTask: ManagedObject = await new ModalBuilder("datastoreSharing/unmountRemoteDatastore")
            .setDimensions([600, 250])
            .open({
               clusterRef: ManagedObject.contextObject,
               datastore: this.selectedDatastore.shareableDatastore.datastore,
            });
      await this.handleTask(modalTask);
      this.selectedDatastore = null;
      this.fetchMountedDatastores();
   }

   async openMountWizard(): Promise<void> {
      const modalTask: ManagedObject = await new ModalBuilder("mountRemoteDatastore")
            .setDimensions([1200, 605])
            .open({
               clusterRef: ManagedObject.contextObject,
            });
      await this.handleTask(modalTask);
      this.fetchMountedDatastores();
   }

   private async handleTask(task: ManagedObject): Promise<any> {
      try {
         return await this.taskMonitorService.getAwaitResult(task);
      } catch (err) {
         Logger.error(err);
         this.errorMessage = err;
      }
   }
}
